#shipping {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.89);
}

#shipping .back {
    text-decoration: none;
    color: white;
    margin: 10px;
}

#shipping form {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: white;
    margin-top: 1vh;
    overflow-y: auto;
    max-height: 80vh;
}

#shipping .error {
    color: red;
    font-size: 0.9rem;
    text-align: center;
}

#shipping form [type="text"] {
    max-width: 500px;
    width: 100vw;
    margin: 1.5vh;
    height: 2rem;
    text-align: center;
    border: 5px solid white;
}

#shipping form [type="text"].empty {
    border: 5px solid red;
}

#question-address {
    color: white;
}

#question-address input {
    width: 2rem;
    height: 2rem;
    margin-right: 10px;
}

#question-address label {
    line-height: 2rem;
}

#shipping .next {
    position: absolute;
    bottom: 10px;
    text-decoration: none;
    color: black;
    background-color: white;
    padding: 5px;
    text-align: center;
    width: 100px;
    font-size: 1.1rem;
    font-weight: bold;
    cursor: pointer;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
}
